.page {
  font-family: Cinzel, serif;
  font-weight: 700;
  margin: 0;
  min-width: 320px;
  scroll-behavior: smooth;
  transition: all .3ms;
}

.page:has(.menu:target) {
  overflow: hidden;
}

.container {
  @include consten-pudding-in-line;
}

.contact:hover {
  color: gray;
}

.contact-white {
  color: orange;
}
